.faqContainer {
  background-color: #f2d5e7;
}

.faqContent {
  display: flex;
  font-size: 50px;
  color: rgba(110, 82, 110, 0.863);
  font-weight: bold;
  justify-content: space-around;
  align-items: center;
}

.faqImage {
  max-height: 500px;
}

.faqText {
  display: flex;
  font-size: 40px;
  font-weight: bold;
  font-family: "CaviarDreams";
  color: rgba(110, 82, 110, 0.863);
  justify-content: center;
}

@media only screen and (max-width: 950px) {
  .faqImage {
    display: none;
  }
}

@media only screen and (max-width: 450px) {
  .faqText {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
    justify-content: center;
    align-items: flex-end;
  }
}

@media only screen and (max-width: 950px) {
  .faqText {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    font-family: "CaviarDreams";
    color: rgba(110, 82, 110, 0.863);
    justify-content: center;
    align-items: flex-end;
  }
}
