.footerContainer {
  height: 200px;
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 20px;
}

.emptyContainer {
  width: 40%;
}

.logoContainer {
  width: 20%;
  display: inline-table;
  align-items: center;
}

.footerLogo {
  max-width: 100px;
}

.footersocialContainer {
  width: 40%;
  margin: 20px;
  display: flex;
  justify-content: center;
}

.trademark {
  font-size: xx-small;
}

@media only screen and (max-width: 450px) {
  .footerContainer {
    height: 200px;
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-content: space-around;
    align-items: center;
  }
}

@media only screen and (max-width: 950px) {
  .footerContainer {
    height: 200px;
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-content: space-around;
    align-items: center;
  }
}
