.utilityContainer {
  background-color: #f9c0c2;
}

.utilityBanner {
  height: fit-content;
  display: flex;
  flex-direction: column-reverse;
  align-content: center;
}

.bannerImage {
  object-fit: contain;
  align-items: center;
}

.utilityHeader {
  font-size: 30px;
  font-weight: bold;
  color: rgba(110, 82, 110, 0.863);
  padding-top: 50px;
  margin-bottom: 10px;
}

.utilityBody {
  display: flex;
  justify-content: space-between;
  color: rgba(110, 82, 110, 0.863);
  padding-left: 75px;
  padding-right: 75px;
}

.textHeader {
  font-size: 26px;
  font-weight: bolder;
}

.textBody {
  font-size: 16px;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: 10px;
}

.treasuryFund {
  border: 4px solid;
  border-radius: 30px;
  padding: 30px;
  margin: 50px;
  width: 33%;
}

.tokenAirdrop {
  border: 4px solid;
  border-radius: 30px;
  padding: 30px;
  margin: 50px;
  width: 33%;
}

.Community {
  border: 4px solid;
  border-radius: 30px;
  padding: 30px;
  margin: 50px;
  width: 33%;
}

