.bannerContainer {
  background-color: white;
  display: flex;
  margin-bottom: -3px;
  margin-right: 100px;
  margin-left: 100px;
}

.gifContainer {
  width: 50%;
  justify-content: center;
}

.DiscordContainer {
  border: 3px solid;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  color: antiquewhite;
  text-decoration-color: black;
}

.discord {
  height: 75px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-family: "CaviarDreams";
}

.braqGIF {
  width: 850px;
  justify-content: center;
}

.braqIntro {
  padding-left: 50px;
  padding-right: 50px;
  width: 50%;
  color: #c6cde8;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.head {
  font-size: 80px;
  font-weight: bold;
}

.bodyText {
  font-size: 35px;
  font-weight: normal;
  margin-bottom: 50px;
}

@media only screen and (max-width: 450px) {
